
@font-face{
  font-family    : 'WorkSans';
  src            : url(/blog/static/fonts/WorkSans-Regular.fd934a4.ttf); /* IE9 Compat; Modes */
  src            : url(/blog/static/fonts/WorkSans-Regular.fd934a4.ttf?#iefix) format('embedded-opentype'),  url(/blog/static/fonts/WorkSans-Regular.8fee706.woff2) format('woff2'),  url(/blog/static/fonts/WorkSans-Regular.33490db.woff) format('woff'); /* Modern Browsers */
  font-style     : normal;
  font-weight    : 400;
  text-rendering : optimizeLegibility;
}
html {
  overflow-y: scroll;
  width: 100%;
  overflow-x: hidden;
}
body {
  margin: 0;
}
.headerContainer {
  background: #615F8B;
  color: white;
}
#app {
  font-family: "WorkSans",sans-serif;
  margin: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: black;
}
.rounded img {
  border-radius: 50%;
}
.header {
  margin-bottom: 10px;
  display: block;
}
.content, .mask, .header {
  max-width: 800px;
  margin: auto;
}
.tag {
  text-decoration: none;
  background: grey;
  border-radius: 12px;
  font-size: 8pt;
  padding: 4px;
  margin: 2px;
  color: black;
}
.backLink {
  font-weight: bold;
  cursor: pointer;
}
.slide-fade-enter-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.slide-fade-leave-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  -webkit-transform: translateY(20px);
          transform: translateY(20px);
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0
}
.mask {
  height: 19px;
  overflow-y:hidden;
}
.mask p{
  margin: 0;
}
.mask p a{
  position: relative;
}
.post {
  border-top: 1px solid;
  padding-bottom: 1em;
  -webkit-transition: background 0.2s;
  transition: background 0.2s;
}
.post:hover {
  background: rgba(127, 127, 127, 0.3);
}
.post p {
  margin-bottom: 0;
}
h3 {
  margin-bottom: 0;
}
.tags {
  margin-bottom: 1em;
}
.rightPanel {
  width: 72px;
  vertical-align: top;
  padding-top: 10px;
  padding-right: 10px;
  padding-left: 10px;
}
.leftPanel, .rightPanel{
  float: left;
}
.leftPanel {
  width: calc(100% - 92px);
}
.leftPanel h3 {
  margin: 8px;
}
@media screen and (max-width: 800px)
{
.rightPanel {
    width: 54px;
    padding-right: 2px;
    padding-left: 2px;
}
.leftPanel {
    width: calc(100% - 58px);
}
}
.contentShort {
  margin-top: 6px;
  display: inline-block;
}
pre code {
  font-size: 75%;
}
























































































































